@at-root {

  @-ms-viewport { /* stylelint-disable-line  at-rule-no-vendor-prefix */
    width: device-width;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

* {
  -webkit-tap-highlight-color: transparent;
}

html, body{
  overflow-x: hidden;
}

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin: 0;
  background-image: url('../img/bg.jpg');
  background-position: center center;
  background-size: cover;
  font-family: $custom-font;
  font-weight: 300;
  word-break: break-word;
  background-color: $body-bg;
}

#id-page{
  position: relative;
}

a {
  text-decoration: none;
  color: $color-main;

  &:hover,
  &:focus {
    color: $color-main;
  }
}

ul {
  @include list-reset();
}

[tabindex='-1']:focus {
  outline: none !important;
}

[role='button'] {
  cursor: pointer;
}

input[type="search"] {
  -webkit-appearance: textfield;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

a,
button,
[role='button'],
input,
label,
select,
textarea {
  touch-action: manipulation;
}

img {
  max-width: 100%;
  height: auto;
}

// Вспомогательные классы

.no-scroll {
  overflow-y: hidden;
}

.nowrap {
  white-space: nowrap;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

// Стили для главного файла
header{  
  padding: rem(40px) 0 0 0;
}
.links{
  width: 100%;
  text-align: right;
}
.logo-nav{
  width: 100%;
  text-align: right;
  color: $black;
  transition: all .5s ease;
  font-size: rem(17px);
  position: relative;
  &:hover, &:active{
    //font-weight: 800;
    color: $hover-color;
    transition: all .5s ease;
    &:after{
      opacity: 1;
    }
  }
  &:focus{
    color: $black;
    text-decoration: none;
  }
  &:after{
    position: absolute;
    content: '';
    width: 50%;
    height: 2px;
    bottom: rem(-5px);
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: $hover-color;
    transition: all .5s ease;
    opacity: 0;
  }
}
.link-center{
  text-align: center;
}

.main-title{
  margin-top: rem(140px);
  h1{
    color: $title-h1-color;
    font-weight: 800;
    font-size: rem(58px);
    margin: 0;
    span{
      color: $hover-color;
    }
  }
  p{
    text-transform: uppercase;
    color: $title-h1-color;
    font-size: rem(21px);
    margin: 0;
    line-height: 1px;
  }
}
.btns-title{
  margin-top: rem(80px);
  button{
    color: #fff;
    font-size: rem(14px);
    font-weight: 800;
    cursor: pointer;
    border: none;
    background-color: transparent;
    border-radius: 31px;
    padding: 15px 25px;
    box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.18);
    &:first-child{
      margin-right: rem(15px);
    }
    &.order{
      background: linear-gradient(to top, #009846 0%, #0ab95a 100%);
      padding: 20px 25px;
    }
    &.scope{
      color: #000;
      border: 5px solid rgba($title-h1-color, .3);
    }
  }
}

.header-image{
  position: absolute;
  top: 0;
  right: 0;
}

.unset{
  position: unset;
}

.h-2{
  font-size: rem(18px);
  font-weight: 800;
  margin: 0;
}

.page-footer{
  margin-top: rem(100px);
  margin-bottom: rem(40px);
  svg{
    fill: rgb(0, 151, 69);
    height: 20px;
    width: 20px;
  }
  .policy{
    p{
      margin: 0;
      font-size: rem(16px)
    }
    a{
      color: $hover-color;
      text-decoration: none;
      transition: all .5s ease;
      &:hover{
        text-decoration: underline;
        transition: all .5s ease;
      }
    }
  }
  .contacts{
    ul{
      li{
        display: flex;
        align-items: center;
        margin-bottom: rem(20px);
        .icons-footer{
          width: 20px;
          height: 20px;
          margin-right: rem(10px);
        }
        span{
          font-weight: 800;
        }
      }
    }
  }
  .copyright{
    text-align: center;
  }
  .social{
    text-align: center;
    a{
      padding: rem(5px) (10px);
      transition: all .5s ease;
      svg{
        fill: rgb(0,0,0);
        transition: all .5s ease;
      }
      &:hover{
        svg{
          fill: rgb(0, 151, 69);
          transition: all .5s ease;
        }
      }
    }
  }
}

.link-phone{
  svg{
    fill: rgb(0,151,69);
  }
  a{
    color: #fff;
    font-size: rem(14px);
    &:hover{
      color: #fff;
      text-decoration: none;
      &:after{
        opacity: 0;
      }
    }
    span{
      font-weight: 800;
    }
  }
}

.link-call{
  a{
    font-weight: 800;
    color: $hover-color;
    &:hover, &:visited{
      color: $hover-color;
    }
  }
}

@media screen and (max-width: 991px){
  .gallery, .scope-block, .tariffs, .bottom-carousel{
    h2{
      margin-left: 0;
      &:before{
        left: -8%;
      }
    }
  }
  .order-sm-first{
    order: 1;
  }
  .order-sm-second{
    order: 2;
  }
  .order-sm-third{
    order: 3;
    width: 100%;
    text-align: center;
  }
}

@media screen and (max-width: 767px){
  .logo_section{
    text-align: center;
    margin-bottom: rem(10px);
  }
  .main-title{
    margin-top: rem(20px);
    h1{
      font-size: rem(40px);
      text-align: center;
    }
    p{
      text-align: center;
    }
  }
  .logo-nav{
    text-align: center;
  }
  .btns-title{
    margin-top: rem(40px);
    width: 100%;
    text-align: center;
  }
  .social-mobile{
    text-align: center;
    margin-top: rem(40px);
    a{
      padding-right: rem(10px);
      transition: all .5s ease;
      svg{
        width: 20px;
        height: 20px;
        transition: all .5s ease;
      }
      &:hover{
        svg{
          fill: rgb(0,151,69);
          transition: all .5s ease;
        }
      }
    }
  }
}

@media screen and (max-width: 454px){
  .page-footer{
    .contacts ul li{
      font-size: rem(14px);
    }
    .policy{
      p, a{
        font-size: rem(14px);
      }
    }
  }
}

@media screen and (max-width: 400px){
  .page-footer{
    .contacts ul li{
      font-size: rem(12px);
    }
    .policy{
      p, a{
        font-size: rem(12px);
      }
    }
    .copyright{
      p{
        font-size: rem(12px);
      }
    }
    svg{
      width: 15px;
    }
    .contacts ul li .icons-footer{
      width: 15px;
    }
    .order-sm-second{
      padding-right: 0;
    }
  }
}

@media screen and (max-width: 329px){
  .page-footer{
    .contacts ul li{
      font-size: rem(10px);
    }
    .policy{
      p, a{
        font-size: rem(10px);
      }
    }
  }
}