// В этом файле должны быть стили для БЭМ-блока gallery, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.gallery {

  h2{
		position: relative;
		margin-left: rem(50px);
		margin-bottom: rem(20px);
		&:before{
			position: absolute;
			content: '';
			height: 3px;
			width: 30px;
			background-color: $hover-color;
			top: 50%;
			left: -5%;
			transform: translate(0, -50%);
		}
	}
	.gallery-carousel{
		text-align: center;
		width: 100%;
		 .item{
			display: inline-block;
			p{
				opacity: 0;
				transition: all .5s ease;
			}
			img{
				padding: 10px;
			}
			&.slick-center{
				transform: scale(1.08);
				transition: all .5s ease;
				img{
					width: 100%;
				}
				p{
					opacity: 1;
					transition: all .5s ease;
				}
			}
			&:last-child{
				margin-right: 0;
			}
		} 
		.slick-dots{
			li{
				width: 15px;
				height: 15px;
				background: #dedede;
				border-radius: 50%;
				&.slick-active{
					background: $hover-color;
				}
				button{
					&:before{
						content: '';
					}
				}
			}
		}
		.slick-arrow{
			position: absolute;
			bottom: 0;
			z-index: 20;
			&:hover{
				svg{
					transition: all .5s ease;
					fill: rgb(0, 151, 69);
				}
			}
		}
	}
	.slick-dots{
		bottom: -50px;
	}
	.arrow-prev-svg{
		left: 20%;
	}
	.arrow-next-svg{
		right: 20%;
	}
}

@media screen and (max-width: 575px){
	.gallery{
		.arrow-prev-svg{
			left: 0;
		}
		.arrow-next-svg{
			right: 0;
		}
	}
}
