.btn {
  box-sizing: border-box;
  display: inline-block;
  margin: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  vertical-align: middle;
  user-select: none;
  text-align: center;
  font-family: inherit;
  text-decoration: none;
  text-transform: none;
  border: 1px solid transparent;
  background-image: none;
  cursor: pointer;

