@font-face {
  font-family: 'Gilroy';
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: local('Gilroy'), local('Gilroy'),
       url('../fonts/Gilroy-ExtraBold.woff2') format('woff2'),
       url('../fonts/Gilroy-ExtraBold.woff') format('woff'),
       url('../fonts/Gilroy-ExtraBold.ttf') format('truetype'),
}

@font-face {
  font-family: 'Gilroy';
  font-style: normal;
  font-weight: 300;
  font-display: swap;
