// В этом файле должны быть стили для БЭМ-блока bottom-carousel, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.bottom-carousel {

  h2{
		position: relative;
		margin-left: rem(50px);
		margin-bottom: rem(20px);
		&:before{
			position: absolute;
			content: '';
			height: 3px;
			width: 30px;
			background-color: $hover-color;
			top: 50%;
			left: -5%;
			transform: translate(0, -50%);
		}
	}
	.caruosel-reviews{
		.reviews{
			position: relative;
			.review{
				padding: 0 rem(10px);
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
				width: 100%;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: flex-end;
				height: 100%;
				&-author{
					width: 100%;
					padding-bottom: rem(5px);
					p{
						text-align: center;
						font-size: rem(10px);
					}
				}
				.job{
					p{
						color: rgba($title-h1-color, .5);
						text-transform: uppercase;
						margin: 0;
						font-size: rem(12px);
					}
				}
				.name{
					h3{
						text-transform: uppercase;
						color: $hover-color;
						font-weight: 800;
						font-size: rem(16px);
						margin: 0;
					}
				}
			}

		}
	}
	.reviews.slick-center{
		transform: csale(1.2);
		width: 762px !important;
		.review{
			&-author{
				width: 100%;
				padding: 0 rem(30px) rem(40px) rem(30px);
				p{
					text-align: center;
					font-size: rem(16px);
				}
			}
			.job{
				p{
					color: rgba($title-h1-color, .5);
					text-transform: uppercase;
					margin: 0;
					font-size: rem(16px);
				}
			}
			.name{
				h3{
					text-transform: uppercase;
					color: $hover-color;
					font-weight: 800;
					font-size: rem(29px);
					margin-top: 4px;
				}
			}
		}
	}
	.slick-track{
		display: flex;
		align-items: center;
	}
	.slick-dots{
		li{
			width: 15px;
			height: 15px;
			background: #dedede;
			border-radius: 50%;
			&.slick-active{
				background: $hover-color;
			}
			button{
				&:before{
					content: '';
				}
			}
		}
	}
}

@media screen and (max-width: 575px){
	.bottom-carousel{
		.reviews{
			&.slick-center{
				.review-author{
					p{
						padding: 0 rem(10px);
					}
				}
			}
		}
	}
}

