// В этом файле должны быть стили для БЭМ-блока tariffs, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.tariffs {
	margin-top: rem(30px);
	h2{
		position: relative;
		margin-left: rem(50px);
		margin-bottom: rem(20px);
		&:before{
			position: absolute;
			content: '';
			height: 3px;
			width: 30px;
			background-color: $hover-color;
			top: 50%;
			left: -5%;
			transform: translate(0, -50%);
		}
	}
	.tariffs-item{
		background-image: url(../img/t.png);
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		overflow: hidden;
		&-name{
			h3{
				text-transform: uppercase;
				font-size: rem(26px);
				color: #fff;
				font-weight: 800;
				margin: 0;
				padding-top: rem(20px);
				padding-bottom: rem(20px);
			}
		}
		&-oldprice{
			width: 50%;
			p{
				font-size: rem(20px);
				font-weight: 800;
				color: #b3b3b3;
				text-transform: uppercase;
				text-align: right;
				margin: 0;
				line-height: 1;
			}
			&.none-price{
				p{
					color: transparent;
				}
			}
		}
		&-price{
			p{
				font-size: rem(40px);
				text-transform: uppercase;
				color: #fff;
				font-weight: 800;
				margin: 0;
				line-height: 1;
				padding-bottom: rem(100px);
			}
		}
	}
	.tariffs-price{
		background: $tariffs-color;
		padding-top: rem(20px);
		.include{
			ul{
				width: 100%;
				text-align: center;
				list-style-image: url('../img/on.png');
				list-style-position: inside;
				li{
					padding-bottom: 10px;
					font-size: rem(16px);
					text-transform: uppercase;
					&:nth-child(even){
						font-weight: 800;
					}
				}
			}
		}
		.all{
			padding-bottom: rem(50px);
		}
		.off{
			ul{
				width: 100%;
				text-align: center;
				list-style-image: url('../img/off.png');
				list-style-position: inside;
				li{
					padding-bottom: 50px;
					font-size: rem(16px);
					text-transform: uppercase;
				}
			}
		}
	}
	.tariffs-order{
		position: relative;
		width: 100%;
		margin-bottom: rem(60px);
		button{
			position: absolute;
			top: rem(-33px);
			left: 50%;
			transform: translate(-50%, 0);
			padding: rem(15px) rem(25px);
			color: #fff;
			font-size: rem(16px);
			border-radius: rem(31px);
			border: none;
			background: linear-gradient(to top, #009846 0%, #0ab95a 100%);
			box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.18);
			cursor: pointer;
		}
	}
}

@media screen and (max-width: 767px){
	.tariffs{
		.tariffs-item-name{
			h3{
				font-size: rem(20px);
			}
		}
		.tariffs-item-price{
			p{
				font-size: rem(30px)
			}
		}
		.tariffs-price{
			.include{ 
				ul{
					li{
						font-size: rem(14px);
					}
				}
			}
		}
		.tariffs-order{
			button{
				width: 100%;
			}
		}
	}
}

@media screen and (max-width: 575px){
	.tariffs{
		.tariffs-item-name{
			h3{
				font-size: rem(26px);
			}
		}
		.tariffs-item-price{
			p{
				font-size: rem(40px);
				padding-bottom: rem(175px);
			}
		}
		.tariffs-price{
			.include{ 
				ul{
					li{
						font-size: rem(16px);
					}
				}
			}
		}
		.tariffs-order{
			button{
				width: 50%;
			}
		}
	}
}

@media screen and (max-width: 380px){
	.tariffs .tariffs-order button{
		width: 100%;
	}
}
