// В этом файле должны быть стили для БЭМ-блока modal, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.modal {
	display: none;
	position: fixed;
  	width: 100%;
  	height: 100vh;
  	top: 0;
  	left: 0;
  	right: 0;
  	bottom: 0;
  	background: rgba(#000, .5);
  	&-window{
  		position: absolute;
  		min-width: 320px;
  		top: 50%;
  		left: 50%;
  		transform: translate(-50%, -50%);
  		width: 50%;
  		background: url(../img/form.png);
  		background-repeat: no-repeat;
  		background-position: center center;
  		background-size: cover;
  		border-radius: 10px;
  		padding: rem(40px);
  		.close{
  			a{
  				position: absolute;
  				right: 12px;
  				top: -25px;
  				svg{
  					fill: rgb(255, 255, 255);
  					width: 20px;
  					height: 20px;
  				}
  			}
  		}
  		h2{
  			margin: 0 0 rem(20px) 0;
  			text-align: center;
  			font-size: rem(19px);
  		}
  		form{
  			display: flex;
  			flex-direction: column;
  			align-items: center;
  			padding: 0 rem(40px);
  			.inputs-section{
  				position: relative;
  				margin-bottom: rem(20px);
  				width: 100%;
  				label{
  					position: absolute;
  					top: 50%;
  					left: 0;
  					transform: translate(0, -50%);
  					width: 20px;
  					height: 20px;
  					svg{
  						fill: rgb(0, 151, 69);
  						width: 20px;
  						height: 20px;
  						margin-left: rem(10px);
  					}
  				}
  				input{
  					border: none;
  					padding: rem(20px) rem(5px) rem(20px) rem(40px);
  					background: $modal-input-color;
  					width: 100%;
  					font-size: rem(16px)
  				}
  			}
			button{
				border: none;
				border-radius: rem(31px);
				padding: rem(20px) rem(40px);
				background: $hover-color;
				color: #fff;
				font-size: rem(16px);
			}
  		}
  	}
}

@media screen and (max-width: 767px){
	.modal {
		.modal-window{
			padding: rem(20px) 0;
			h2{
				margin: 0 rem(10px) rem(20px) rem(10px);
			}
		}
	}
}

@media screen and (max-width: 655px){
	.modal {
		.modal-window{
			h2{
				font-size: rem(16px);
			}
			form{
				.inputs-section{
					input{
						font-size: rem(14px);
					}
				}
				button{
					font-size: rem(14px);
					padding: rem(15px) rem(30px);
				}
			}
		}
	}
}
